<template>
    <div id="index">
        <ul>
          <li key='1'>
            <img :src="imgurl" />
          </li>
        </ul>
    </div>
</template>
<script>
import Viewer from 'viewerjs';
import 'viewerjs/dist/viewer.css';
export default {
  name: 'Viewer',
  props: ['imgurl'],
  mounted () {
    	var This = this;
    const ViewerDom = document.getElementById('index');
    const viewer = new Viewer(ViewerDom, {
      toolbar: false,
      navbar: false,
      zIndex: 9999,
      backdrop: true,
      title: false,
      hide: function () {
            	viewer.destroy();
            	This.$emit('viewerListen');
      }
    });
    window.layer.closeAll();
    viewer.show();
  }
};
</script>
<style lang="less">
	.viewer-container.viewer-fixed.viewer-fade.viewer-transition.viewer-in{
		background:rgba(0,0,0,0.5);
		.viewer-title{
			line-height:30px;
			padding-bottom:20px;
			font-size:14px;
		}
	}
	#index {
		display:none;
	    width: 100%;
	    ul{
	        li {
	            width: 24%;
	            height: 200px;
	            margin-right: 1%;
	            margin-bottom: 20px;
	            cursor: pointer;
	            display: inline-block;
	            &:nth-child(4n) {
	                margin-right: 0;
	            }
	            img {
	                height: 100%;
	                width: 100%;
	                object-fit: cover;
	            }
	        }
	    }
	}
</style>



